<script>
  import { getContext } from 'svelte'

  import SearchIcon from './SearchIcon.svelte'
  const { keyword, isLoading } = getContext('appState')

  let value = $keyword;

  const doSearch = () => {
    keyword.set(value)
  }
</script>

<form class="box" on:submit|preventDefault={doSearch}>
  <input
    class="input"
    type="text"
    placeholder="Search emoji"
    disabled="{$isLoading}" bind:value aria-label="Search emoji" />
  <button class="button" class:loading="{$isLoading}" on:click={doSearch} aria-label="Do search">
    <SearchIcon />
  </button>
</form>

